*:focus {
  outline: none;
}

body {height: 100vh;
  background-image: linear-gradient(to bottom, rgb(254, 254, 253) , rgb(191, 148, 211));}


.app-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
.app-wrapper__app {
  display: flex;
  flex-direction: column;
  align-items: center;
  // background-color: rgb(91, 54, 148);
  background-image: linear-gradient(to bottom, rgb(254, 254, 253) , rgb(191, 148, 211));
  border-radius: 15px;
  width: 30vw;
  height: 100%;
  min-width: 500px;

.app__input-admit {
  margin-top: 20px;
}

  .app__input-admit--input {
    width: 70%;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
    text-align: center;
    padding: 5px 10px;
    font-size: 16px;
    border: 1px solid gray;
    border-right: none;
    // background-color: rgb(242, 255, 0);
}

.app__input-admit--admit {
  border: 1px solid gray;
  font-size: 16px;
  padding: 5px 15px;
  // background-image: linear-gradient(to right, rgb(242, 255, 0) , rgb(255, 149, 0));
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
 
}

  .app__tasks-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-inline-start: 0px;
    gap: 10px;
    color: white;
    text-decoration: none;
    
    .tasks-container__task {
      // align-items: center;
      justify-content: center;
      display: flex;
      border-radius: 20px;
    background-color: rgb(57, 192, 201);
    padding: 10px 30px;
    height: 100%;
    width: 300px;
    text-align: center;
    list-style-type: none;
  
    .task__isCompleted {
      .isCompleted__switch {
        position: relative;
      display: inline-block;
      width: 50px;
      height: 20px;
      margin: 0 10px;
      background-color:transparent;}
      .isCompleted__input {display: none;
        &:checked + .isCompleted__slider {
          background-color: rgb(59, 182, 59)
        }
        &:checked + .isCompleted__slider:before {
          transform: translateX(25px);
        }
      }
      .isCompleted__slider {position: absolute;
        cursor:pointer;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color:  red;
        transition: .3s;
        border-radius: 34px;
       
      &::before {position: absolute;
        content: "";
        height: 20px;
        width: 25px;
        left:0px;
        // bottom: 5px;
        background-color: white;
        transition: .4s;
        border-radius: 50px;}
      }
    }
    .task__delete {
      padding-left: 20px;
      font-size: 20px;
      min-height: 10px;
      color: blueviolet;
      cursor: pointer;
    }
    }

  }

      }
    }

   